@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.0.0-beta3/css/all.min.css);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#header {
  background-color: #73626b43;
  /* display: flex; */
  display: none;
  flex-flow: row wrap;
  justify-content: center;
  position: fixed;
  width: 100%;
  max-width: 800px;
  padding: 0 3em;
  z-index: 10;
}

.header-item {
  font: 100%/1.6 "Nanum", "Trebuchet MS", Helvetica, sans-serif;
  font-size: 1.5em;
  padding: 0.75em 2em;
}

.social-item {
  line-height: 1;
  font-size: 1.5em;
  margin: 0.25em 0.1em;
}

.social-link {
  color: #73626b;
}

#sidebar,
#sidebar-padding {
  padding: 0.4em 0;
  margin: 0;
}

#sidebar {
  background-color: #73626b43;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: space-around;
  position: -webkit-sticky;
  position: sticky;
  align-self: flex-start;
  margin-top: 30vh;
  top: 0px;
}

#sidebar.social-link {
  color: white;
}

#sidebar-padding {
  color: transparent;
}

#react {
  color: #73626b93;
  font-size: 0.75em;
  text-align: right;
  /* font-weight: bold; */
  font-family: "Open Sans", "Trebuchet MS", Helvetica, sans-serif;
}

#react-icon {
  font-size: 0.9em;
  margin: 0 1em 0 0.2em;
  color: #73626b93;
}

.section {
  width: calc(100% - 6em);
  padding: 3em;
  padding-top: max(5em, 9vh);
}

.section.colored {
  background-color: #eaf0f3;
}

.section-title {
  font: 100%/1.6 "Quicksand", "Trebuchet MS", Helvetica, sans-serif;
  font-size: 3em;
  color: #73626b;
}

.resume-button {
  background-color: #73626b43;
  height: 2.5em;
  color: #73626b;
  font-family: "Open Sans", "Trebuchet MS", Helvetica, sans-serif;
  font-size: 0.85em;
  padding: 0 0.8em;
  margin: 1em;
  display: flex;
  align-items: center;
  justify-content: space-between;

  -webkit-animation: 1s linear 0s infinite bounce;

          animation: 1s linear 0s infinite bounce;
}

.resume-button span,
i {
  margin: 0 0.2em;
}

#intro-section {
  padding-top: max(10em, 22vh);
}

#intro-greeting {
  color: #73626b;
  font-size: 2em;
  height: 1.1em;
  margin-left: 0.1em;
}

#intro-title {
  font-size: 4em;
  height: 1.8em;
}

#intro-box {
  flex-flow: row wrap;
}

/* left side contents */

#intro-box-right {
  margin: 1em;
  margin-right: 3em;
  display: flex;
  align-items: center;
  flex-flow: column nowrap;
}

#pfp {
  object-fit: cover;
  height: 13em;
  width: 13em;
  max-height: 250px;
  max-width: 250px;
}

/* right side contents */

#blurb-box {
  height: 100%;
  flex-grow: 0.4;
  display: flex;
  flex-flow: column nowrap;
}

.blurb {
  height: 2em;
  padding: 0.2em 0.5em;
  margin: 0.25em;
  width: 60%;
}

.blurb.one {
  background-color: #a7cbd9;
  text-align: left;
  padding-right: 5em;
  align-self: flex-start;
}

.blurb.two {
  background-color: #dabb7b;
  text-align: right;
  padding-left: 5em;
  align-self: flex-end;
}

.experience-tile {
  padding-top: 1.8em;
  padding-bottom: 1em;
}

.experience-company {
  color: #5c8ea1;
  font-size: 1.25em;
  padding-bottom: 0.3em;
}

.experience-title {
  background-color: #dabb7b;
  padding-left: 1em;
  width: 20em;
}

.experience-container-middle,
.experience-container-bottom,
.experience-container-top {
  display: flex;
  flex-flow: row nowrap;
  margin-bottom: 0.5em;
}

.experience-container-middle {
  justify-content: space-between;
}

.experience-container-small {
  padding: 0.5em;
  display: flex;
  justify-content: center;
  flex-flow: column nowrap;
  flex-grow: 0;
  width: 3em;
}

.experience-summary {
  background-color: #a7cbd97e;
  padding: 0.5em 1em;
  padding-right: 5em;
  width: 100%;
}

.experience-dates,
.experience-technologies {
  font-size: 0.8em;
  padding: 0 1.5em;
}

.experience-dates {
  line-height: 1;
  flex-grow: 1;
  align-self: flex-end;
  text-align: right;
}

.experience-technologies {
  background-color: #dabb7b7e;
  text-align: right;
  width: 25em;
}

.experience-bullets-container {
  height: 0;
  overflow: hidden;
  transition: 0.5s ease;
}

.experience-bullets {
  padding: 0.5em 0 1em 0;
  margin: 0 5%;
}

.experience-bullets-toggle {
  background-color: #73626b7e;
  height: 1.5em;
  width: 3em;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;

  -webkit-animation: 1s linear 0s infinite bounce;

          animation: 1s linear 0s infinite bounce;
}

.toggle-arrow {
  height: 30%;
  opacity: 75%;
}

.toggle-arrow.expanded {
  -webkit-transform: rotateX(180deg);
          transform: rotateX(180deg);
}

.toggle-arrow.one {
  -webkit-animation: flip 0.8s;
          animation: flip 0.8s;
}

.toggle-arrow.two {
  -webkit-animation: flip-reverse 0.8s;
          animation: flip-reverse 0.8s;
}

@font-face {
  font-family: Quicksand;
  src: url(/static/media/Quicksand-Regular.cffc2790.ttf);
}

html,
body,
#root {
  height: 100%;
  width: 100%;
}

html {
  scroll-behavior: smooth;
}

body {
  color: #402a01;
  background-color: white;
  font: 100%/2 "Quicksand", "Trebuchet MS", Helvetica, sans-serif;
  margin: 0;
  padding: 0;
}

a {
  color: #402a01;
  text-decoration: none;
}

/* page contents */

#content {
  display: flex;
  align-items: center;
  flex-flow: column nowrap;
  padding-bottom: 5em;
}

#page-contents {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

#page-sections {
  width: calc(80% + 6em);
  max-width: calc(800px + 6em);
  display: flex;
  flex-flow: column nowrap;
}

li {
  font-size: 0.5em;
}

li span {
  font-size: 1.8em;
}

a.formatted {
  color: #5c8ea1;
  text-decoration: underline;
}

.bold {
  font-weight: bold;
}

.center-contents {
  /* centers contents within this div */
  display: flex;
  align-items: center;
  justify-content: center;
}

/* colors */

#color-scheme {
  width: 200px;
  display: flex;
  height: 10vh;
}

.color {
  height: 100%;
  flex-grow: 1;
}
.color.one {
  background-color: #a7cbd9;
}

.color.two {
  background-color: #dabb7b;
}

.color.three {
  background-color: #73626b;
}

.color.four {
  background-color: #402a01;
}

